<template>
  <div>
    <h1> {{$route.params.appname}} </h1>
    <cmprc v-if="cmon"></cmprc>
  </div>
</template>

<script>
import cmprc from '../../docs/colourmatrix_privacy.md'

export default {
  name: 'MyApp',
  data () {
    return {
      mylist: [
        1, 2, 3, 5, 8
      ],
      cmon: false
    }
  },
  components: {
    cmprc
  },
  beforeRouteUpdate (to, from, next) {
    if (to.params.appname !== 'ColourMatrix' && to.params.appname !== 'CountingDevice') {
      next('/404')
    } else {
      this.cmon = to.params.appname === 'ColourMatrix'
      next()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (to.params.appname !== 'ColourMatrix' && to.params.appname !== 'CountingDevice') {
      next('/404')
    } else {
      next(vm => {
        vm.cmon = to.params.appname === 'ColourMatrix'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
h1, h2 {
  font-weight: normal;
}
</style>



